/* googles fonts */
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;700&family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap');

*
{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root
{
    --background-primary: hsl(218, 23%, 23%);
    --background-secondary: hsl(220, 26%, 14%);
    --action-primary: hsl(216, 15%, 52%);
    --action-secondary: hsl(218, 23%, 23%);
    --foreground-primary: hsl(210, 38%, 95%);
    --foreground-secondary: hsl(211, 25%, 84%);
    --foreground-tertiary: hsl(214, 20%, 69%);
    --accent: hsl(229, 76%, 66%);
    --white: #fff;
}

html
{
    font-size: 62.5%;
    scroll-behavior: smooth;
}

body
{
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    overflow-x: hidden;
}

button
{
    cursor: pointer;
}

a
{
    text-decoration: none;
    cursor: pointer;
}

.container
{
    max-width: 130rem;
    margin: auto;
}

.center 
{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.section
{
    padding: 5rem;
    background-color: var(--background-secondary);
}

.common-heading
{
    font-size: 4rem;
    color: var(--foreground-primary);
    font-weight: 500;
    text-align: center;
    position: relative;
    padding-bottom: 1rem;
}

.common-heading::before
{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    background-color: var(--accent);
    height: 1.2rem;
    width: 1.2rem;
    border-radius: 50%;
}

.common-heading::after
{
    content: '';
    position: absolute;
    left: 0;
    bottom: 5%;
    background-color: var(--accent);
    height: .5rem;
    width: 80%;
    border-radius: 1rem;
}

/* paginations style  */
.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root
{
    color: var(--foreground-primary) !important;
    font-size: 1.5rem !important;
    transition: all .5s;
}

.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root svg
{
    font-size: 2rem !important;
}

.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root
{
    color: var(--foreground-primary) !important;
    font-size: 1.8rem !important;
}

.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected
{
    background: var(--accent) !important;
    color: var(--foreground-primary) !important;
    transition: all .5s;
}

.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root.Mui-selected:hover,
.css-bf9wz-MuiButtonBase-root-MuiPaginationItem-root:hover
{
    background: var(--foreground-primary) !important;
    color: var(--accent) !important;
}

/* responsive  */
@media screen and (max-width: 600px)
{
    html
    {
        font-size: 52.5%;
    }

    .section
    {
        padding: 3rem;
    }
}